import React, {useEffect, useState} from 'react';
import { CTable, CTableBody, CTableDataCell, CTableHead, CTableHeaderCell, CTableRow } from '@coreui/react';

import './patient.scss';

import CIcon from '@coreui/icons-react'
import {
  cilTrash,
} from '@coreui/icons'
import { Link } from 'react-router-dom';



const PatientList=()=> {
  const [patient, setPatient] = useState([])

  useEffect(() => {
    fetch("http://13.233.39.128:5000/api/user/list", {
        method: 'POST', // or 'PUT'
        headers: {
            'Content-Type': 'application/json',
        }
    }).then(e=>e.json()).then((data)=>{
        console.log(data);
        setPatient(data.data);
    })
  }, []);

  const deletePatient = (arg)=>{
    window.confirm("Are you Sure you want to delete this");
  }
  

  return (<>
        <div className='d-flex justify-content-start align-items-center mb-3'>
            <h2 className='mb-3'>Patient List</h2>
        </div>
 
    <div className='arTableWrapper'>
        <CTable>
            <CTableHead>
                <CTableRow>
                <CTableHeaderCell width={220} scope="col">UID</CTableHeaderCell>
                <CTableHeaderCell scope="col">Full Name</CTableHeaderCell>
                <CTableHeaderCell scope="col">Mobile No</CTableHeaderCell>
                <CTableHeaderCell scope="col">Email</CTableHeaderCell>
                <CTableHeaderCell width={240} scope="col">Created Date</CTableHeaderCell>
                <CTableHeaderCell width={60} scope="col">Action</CTableHeaderCell>
                </CTableRow>
            </CTableHead>
            <CTableBody>
                {patient.map((i, k)=>(
                    <CTableRow key={k}>
                        <CTableHeaderCell scope="row"><Link to={"/patients/"+i._id}>{i.uid}</Link></CTableHeaderCell>
                        <CTableDataCell>{i.first_name}{' '}{i.last_name}</CTableDataCell>
                        <CTableDataCell>{i.mobile}</CTableDataCell>
                        <CTableDataCell>{i.email}</CTableDataCell>
                        <CTableDataCell>{i.created_at}</CTableDataCell>   
                        <CTableDataCell className='text-center'> <div onClick={()=>{deletePatient(i._id)}} className='deleteBtnAr'><CIcon icon={cilTrash} /></div></CTableDataCell>                        
                    </CTableRow>
                ))}                
            </CTableBody>
        </CTable>
    </div>
    </>)
}
export default PatientList;